<template>
  <div class="loadData">
	<div class="scan">
		<img src="../assets/scanline.png">
		<div class="male">
			<span class="flashPoint topPoint"></span>
			<span class="flashPoint toppointS"></span>
			<span class="flashPoint toppointBeside"></span>
			<span class="flashPoint toppointBesideR"></span>
			<span class="flashPoint toppomiddle"></span>
			<span class="flashPoint topeyeR"></span>
			<span class="flashPoint topeyeL"></span>
			<span class="flashPoint nuisePoint"></span>
			<span class="flashPoint nuisePointR"></span>
			<span class="flashPoint nuisePointL"></span>
			<span class="flashPoint mouseTop"></span>
			<span class="flashPoint mouseLeft"></span>
			<span class="flashPoint mouseRight"></span>
			<span class="flashPoint mouseBeside"></span>
			<span class="flashPoint mouseBesideR"></span>
			<span class="flashPoint mouseBottomL"></span>
			<span class="flashPoint mouseBottomR"></span>
			<span class="flashPoint mouseBottom"></span>

		</div>
	</div>
	<div class="bottom">
		<img src="../assets/bottonNumText.png">
	</div>
  </div>
</template>

<script>




export default {
 data(){
 	return {

 	}
 },
 mounted(){
 	this.homeinit()
 	this.backNum()
 	this.flashPoint()
 },

methods:{
	homeinit(){		
		let _this = this
		$('.loadData .scan img ').animate({top:"6.666667rem"},2000,function(){
			$('.loadData .scan img ').animate({top:"0"},2000,function(){
				 _this.homeinit()
			})
		})

	},
	flashPoint(){
		
			let _this = this

		$('.male span').each(function(index){
			var Flash=Math.random().toFixed(1)
			var flash=Math.random().toFixed(1)
			$(this).css('opacity',Flash)
			$(this).animate({'opacity':flash},500,function(){
				$(this).animate({'opacity':flash},500,function(){
					if(index==$('.male span').length-1){
						_this.flashPoint()
					}
				})
			})
		})
	},
	backNum(){
		let _this = this
		setTimeout(function(){
			 _this.$router.push({path: '/loading'});
		},6000)
		
	}	
}
}

</script>

<style lang="scss">
	.loadData{
		background: #12142e;
		width:100vw;
		height:100vh;
		overflow: hidden;
		.male{
			position:relative;
			background: url('../assets/male.png') no-repeat;
			width:375px;
			height:624px;
			background-size:contain;
			background-position: bottom;
			margin: 0 auto;
				.topPoint{
					left:calc(50% - 3px);
					top:30px;
				}
				.toppointS{
					left:calc(50% - 3px);
					top:100px;
				}
				.toppointBeside{
					left:64px;
					top:233px;
				}
				.toppointBesideR{
					right:64px;
					top:233px;
				}
				.toppomiddle{
					left:calc(50% - 3px);
					top:263px;
				}
				.topeyeR{
					top:295px;
					right:60px;
				}
				.topeyeL{
					top:295px;
					left:60px;
				}
				.nuisePoint{
					left:calc(50% - 3px);
					top:372px
				}
				.nuisePointR{
					right:135px;
					top:395px;
				}
				.nuisePointL{
					left:145px;
					top:395px;
				}
				.mouseTop{
					left:calc(50% - 3px);
					bottom:180px;
				}
				.mouseLeft{
					left:140px;
					bottom:160px;
				}
				.mouseRight{
					right:135px;
					bottom:160px;
				}
				.mouseBeside{
					left:118px;
					bottom:132px;
				}
				.mouseBesideR{
					right:106px;
					bottom:132px;
				}
				.mouseBottomL{
					left:160px;
					bottom:123px;
				}

				.mouseBottomR{
					right:150px;
					bottom:123px;
				}
				.mouseBottom{
					left:calc(50% - 3px);
					bottom:55px;
				}


		}
		.scan{
			position: relative;
			
			background: url('../assets/scan.png');
			background-size:cover;
			width:630px;
			height:624px;
			margin:160px auto 0;
				img{
					top:0;
					position:absolute;
					width: 100%;
				}
		}
		.bottom{
			margin-top:52px;
			text-align: center;
			img{
				width:520px;
			}
		}
	}
.flashPoint{
	background:#4896db;
	width:13px;
	height:13px;
	position:absolute;
	border-radius: 20px;
	display: inline-block
}

</style>



