<template>
  <div class="backNum">
	<div class="num">	
	<img src="../assets/num_3.png">
	<img src="../assets/num_2.png">
		<img src="../assets/num_1.png">
		
	</div>
	 <div class="footButton">
	  	<h1 class="animateText">
  		<div class="animateL">
  			<span class="step1"></span>
  			<span class="step2"></span>
  			<span class="step3"></span>
  			<span class="step4"></span>	
  		</div>	
  		<div class="animateR">
  			<span class="step1"></span>
  			<span class="step2"></span>
  			<span class="step3"></span>
  			<span class="step4"></span>	

  		</div>

  		<p><img src="../assets/NumdecorText.png"></p>
  	
  	</h1>
  	<h2>
  		<img src="../assets/bottonNumText.png">
  	</h2>
  </div>
  </div>
</template>

<script>




export default {
 data(){
 	return {

 	}
 },
 mounted(){
 	this.homeinit()
 	this.backNum()
 },

methods:{
	homeinit(){		
			$('.backNum .step1').animate({width:'2.666667rem'},1500,function(){
				$('.backNum .step2').animate({width:'0.666667rem'},function(){
					$('.backNum .step3').animate({width:'2rem'},function(){
						$('.backNum .step4').animate({opacity:'1'},function(){
							$('.backNum .animateText p').animate({opacity:'1'},function(){
								
								$('.backNum .footButton h2').animate({opacity:'1'})
							})
						})
					})
				})
			})		
	},
	backNum(){
		let _this = this
		var i=0;
		setTimeout(function(){
			var time=window.setInterval(function(){
			$('.num img').hide();
			$('.num img').eq(i).show()
			i++;
			if(i>=4){
				window.clearInterval(time)
				console.log(this.$router)
				_this.$router.push({path: '/loadData'});
			}
		},1000)

		},2000)
		
	}	
}
}

</script>

<style lang="scss">

	.backNum{
		width:100vw;
		height:100vh;
		background-image:url('../assets/numBanner.jpg');
		background-size:cover;
			.num{
				position: absolute;
				width:100%;
				text-align: center;
				top:calc(50% - 100px );
					img{
						display:none;
						width:90px;
						height:120px;
					}
			}
				.footButton{
			position: absolute;
			bottom:100px;
				h2{	
					font-size:0;
					p{
						display:inline-block;
					}
					width: 100vw;
					text-align: center;
					.leftB{
						span{
							display: inline-block;
							width:42px;
							height:54px;
							background-image:url('../assets/slideNext.png');
							background-size:cover;
							transform:rotate(180deg);
							vertical-align: middle;
						}
						span:nth-child(1) {
							opacity: 0.5
						}
						span:nth-child(2) {
							opacity: 0.8
						}
						span:nth-child(3) {
							opacity: 1
						}
					}
					.rightB{
						span{
							display: inline-block;
							width:42px;
							height:54px;
							background-image:url('../assets/slideNext.png');
							background-size:cover;
							vertical-align: middle;
						}
						span:nth-child(3) {
							opacity: 0.5
						}
						span:nth-child(2) {
							opacity: 0.8
						}
						span:nth-child(1) {
							opacity: 1
						}
					}
					img{

					vertical-align:middle;
					width:390px;
				}}
				h1{
					
					position:relative;
					width:100vw;
					height:94px;
					margin-bottom:60px;
					text-align:center;	
					p{
						opacity:0;
						line-height:94px;
						img{
							text-align:center;
							width:171px;
						}
					}
				.animateL{
					display:inline-block;
					left:0;
					width:50%;
					position:absolute;
					height: 94px;
					.step1{
						background:#e6ca45;
						width:0;
						height:5px;
						display:inline-block;
						left:0;
						position:absolute;
						top: 45px;
						z-index:1;
					}
					.step2{
						position:absolute;
						display:inline-block;
						height:5px;
						transform:rotate(45deg);
						background:#384664;
						width: 0px;
						top: 62px;
						z-index:0;
						left:150px;
					}
					.step3{
						position:absolute;
						display:inline-block;
						height:5px;
						left:190px;
						background:#384664;
						width: 0;
						top: 79px;
						z-index:0;
					}
					.step4{
						display:none;
						position:absolute;
						display:inline-block;
						height:15px;
						left:340px;
						background:#e6ca45;
						width: 15px;
						border-radius: 50%;
						top: 72px;
						z-index:0;
						opacity:0;

					}
				}
				.animateR{
					display:inline-block;
					width:50%;
					position:absolute;
					height: 94px;
					right:0;
					.step1{
						background:#e6ca45;
						width:0;
						height:5px;
						display:inline-block;
						right:0;
						position:absolute;
						top: 45px;
						z-index:1;
					}
					.step2{
						position:absolute;
						display:inline-block;
						height:5px;
						transform:rotate(45deg);
						background:#384664;
						width: 0px;
						top: 28px;
						z-index:0;
						right:150px;
					}
					.step3{
						position:absolute;
						display:inline-block;
						height:5px;
						right:190px;
						background:#384664;
						width: 0;
						top: 12px;
						z-index:0;
					}
					.step4{

						position:absolute;
						display:inline-block;
						height:15px;
						right:340px;
						background:#e6ca45;
						width: 15px;
						border-radius: 50%;
						top: 8px;
						z-index:0;
						opacity:0;

					}
				}
				}
		}
	}


</style>



