import Vue from 'vue'
import Router from 'vue-router'
import home from '../page/home'
import backNum from '../page/backNum'
import loadData from '../page/loadData'
import loading from '../page/loading'
import result from '../page/result'

Vue.use(Router)

export default new Router({
  routes: [
     {
      path: '/',
      name: 'home',
      component: home
    },
     {
      path: '/backNum',
      name: 'backNum',
      component: backNum
    },
     {
      path: '/loadData',
      name: 'loadData',
      component: loadData
    },
    {
      path: '/loading',
      name: 'loading',
      component: loading
    },
      {
      path: '/result',
      name: 'result',
      component: result
    }
  ]
  ,
      mode: 'hash'
})



