<template>
  <div id="app">
    <router-view></router-view> 
    <i @click="pause()" class="icon-music icon-pause"></i>
      <audio  loop="loop" src="./assets/music.mp3" id="music" ></audio>
      <div class="backNum"></div>
        <div class="scan"></div>
        <div class="result"></div>
        <div class="resultInner"></div>
  </div>

</template>

<script>
export default {
  name: 'app',
  data(){
  	return {

  	}
  },
  mounted(){},
  methods:{
  	pause(){
  		var music = document.getElementById('music')
  		 if(music.paused)    	              
  		  {
  		    $('.icon-music').addClass('icon-pause') 
  		    $('.icon-music').removeClass('icon-musicP')                  
      		music.play();//audio.play();// 这个就是播放  
		  }else{
		   music.pause();// 这个就是暂停
		   $('.icon-music').removeClass('icon-pause')
		   $('.icon-music').addClass('icon-musicP')
		  }
  	}
  }
}
</script>

<style lang="scss">
@import '/modules/css/common.scss';
#app{
	width:100vw;
	height:100vh;
	overflow: hidden;
	.icon-music{
		position: fixed;
		top:144px;
		left:30px;
		border-radius: 50%;
		width:54px;
		height:54px;
		background-size:30px;
		background-repeat: no-repeat;
		background-position: center;
		border:2px solid #f5d748;
		cursor:point;
		background-image:url('./assets/music.png');
		padding:10px;	
	}
	.icon-musicP{
		background-image:url('./assets/pause.png');
	}
	.icon-pause{
			animation:rotating 3s linear infinite;
			

}
.pause{
animation-play-state:paused;
-webkit-animation-play-state:paused; /* Safari 和 Chrome */
}
}
@keyframes rotating{
from{transform:rotate(0)}
to{transform:rotate(360deg)}
}
.backNum{
		background-image:url('./assets/numBanner.jpg');
		background-size:cover;
	}
.scan{
	background: url('./assets/scan.png');
}
.result{
	background-image:url('./assets/resBack.png');
}
.resultInner{
	background-image:url('./assets/innerBack.png');
}
.result{
		background-image:url('./assets/resBack.png');
}
</style>



