<template>
  <div class="home">
  <audio  loop="loop" src="../assets/music.mp3" id="music" autoplay="autoplay"></audio>
  <div class="footButton">
  	<h1 class="animateText">
  		<div class="animateL">
  			<span class="step1"></span>
  			<span class="step2"></span>
  			<span class="step3"></span>
  			<span class="step4"></span>	
  		</div>	
  		<div class="animateR">
  			<span class="step1"></span>
  			<span class="step2"></span>
  			<span class="step3"></span>
  			<span class="step4"></span>	

  		</div>
  		<p><img src="../assets/decorText.png"></p>
  	
  	</h1>
  	<h2>
  	<p class="leftB">
	  	<span ></span>
  		<span ></span>
  		<span ></span>	
  	</p>

		<img @click="backNum" src="../assets/startButton.png">
	<p class="rightB">
		<span></span>
		<span></span>
		<span></span>
	</p>
  	</h2>
  </div>
  </div>
</template>

<script>




export default {
 data(){
 	return {

 	}
 },
 mounted(){
 	this.homeinit()
 	this.gifSlide()
 },

methods:{
	homeinit(){		
			$('.home .step1').animate({width:'2.666667rem'},1500,function(){
				$('.home .step2').animate({width:'0.666667rem'},function(){
					$('.home .step3').animate({width:'2rem'},function(){
						$('.home .step4').animate({opacity:'1'},function(){
							$('.home .animateText p').animate({opacity:'1'},function(){
								$('.home .footButton h2').animate({opacity:'1'})
							})
						})
					})
				})
			})
	},
	gifSlide(){
		var i=0;
		this.time=window.setInterval(function(){
			if(i>=3){
				i=0
			}
			$('.home h2 span').each(function(){
				$(this).css('opacity','1')
				
			})
			$('.home .leftB span').eq(2-i).css('opacity','0.5')
			$('.home .rightB span').eq(i).css('opacity','0.5')
			i++;
		},300)
	},
	backNum(){
		console.log(this.$router)

		this.$router.push({path: '/backNum'});
	
		console.log(this.$router)
	}
}
}

</script>

<style lang="scss">
	.home{
		background-image: url('../assets/startBanner.jpg');
		background-size:cover;
		width:100%;
		height:100vh;

	}
			.footButton{
			position: absolute;
			bottom:100px;
				h2{	
					opacity:0;
					font-size:0;
					p{
						display:inline-block;
					}
					width: 100vw;
					text-align: center;
					.leftB{
						span{
							display: inline-block;
							width:42px;
							height:54px;
							background-image:url('../assets/slideNext.png');
							background-size:cover;
							transform:rotate(180deg);
							vertical-align: middle;
						}
						span:nth-child(1) {
							opacity: 0.5
						}
						span:nth-child(2) {
							opacity: 0.8
						}
						span:nth-child(3) {
							opacity: 1
						}
					}
					.rightB{
						span{
							display: inline-block;
							width:42px;
							height:54px;
							background-image:url('../assets/slideNext.png');
							background-size:cover;
							vertical-align: middle;
						}
						span:nth-child(3) {
							opacity: 0.5
						}
						span:nth-child(2) {
							opacity: 0.8
						}
						span:nth-child(1) {
							opacity: 1
						}
					}
					img{

					vertical-align:middle;
					width:390px;
				}}
				h1{
					
					position:relative;
					width:100vw;
					height:94px;
					margin-bottom:60px;
					text-align:center;	
					p{
						opacity:0;
						line-height:94px;
						img{
							text-align:center;
							width:286px;
						}
					}
				.animateL{
										display:inline-block;
					left:0;
					width:50%;
					position:absolute;
					height: 94px;
					.step1{
						background:#e6ca45;
						width:0;
						height:5px;
						display:inline-block;
						left:0;
						position:absolute;
						top: 45px;
						z-index:1;
					}
					.step2{
						position:absolute;
						display:inline-block;
						height:5px;
						transform:rotate(45deg);
						background:#384664;
						width: 0px;
						top: 62px;
						z-index:0;
						left:150px;
					}
					.step3{
						position:absolute;
						display:inline-block;
						height:5px;
						left:190px;
						background:#384664;
						width: 0;
						top: 79px;
						z-index:0;
					}
					.step4{
						display:none;
						position:absolute;
						display:inline-block;
						height:15px;
						left:340px;
						background:#e6ca45;
						width: 15px;
						border-radius: 50%;
						top: 72px;
						z-index:0;
						opacity:0;

					}
				}
				.animateR{
					display:inline-block;
					width:50%;
					position:absolute;
					height: 94px;
					right:0;
					.step1{
						background:#e6ca45;
						width:0;
						height:5px;
						display:inline-block;
						right:0;
						position:absolute;
						top: 45px;
						z-index:1;
					}
					.step2{
						position:absolute;
						display:inline-block;
						height:5px;
						transform:rotate(45deg);
						background:#384664;
						width: 0px;
						top: 28px;
						z-index:0;
						right:150px;
					}
					.step3{
						position:absolute;
						display:inline-block;
						height:5px;
						right:190px;
						background:#384664;
						width: 0;
						top: 12px;
						z-index:0;
					}
					.step4{

						position:absolute;
						display:inline-block;
						height:15px;
						right:340px;
						background:#e6ca45;
						width: 15px;
						border-radius: 50%;
						top: 8px;
						z-index:0;
						opacity:0;

					}
				}
				}
		}
@-webkit-keyframes rotation{
from {-webkit-transform: rotate(0deg);}
to {-webkit-transform: rotate(360deg);}
}


</style>



